import React from 'react'
import Link from 'gatsby-link'
import HomeHero from '../assets/main-hero.png'

const IndexPage = () => (
  <div>
    <Link to="/our-company">
      <img src={HomeHero} />
    </Link>
  </div>
)

export default IndexPage



